/* -- Slideout Sidebar -- */
.slideout-sidebar {
  position: fixed;
  top: 0;
  left: calc(-120%);
  z-index: 0;
  width: calc(100% - 40px);
  height: 100%;
  padding: 20px;
  background-color: #212121;
  transition: all 300ms ease-in-out;
}

/* -- Slideout Sidebar -- */
.slideout-sidebar {
  top: 0;
  left: calc(-120%);
  z-index: 0;
  width: calc(100% - 40px);
  height: 100%;
  padding: 20px;
  background-color: #212121;
  transition: all 300ms ease-in-out;
}

.slideout-sidebar ul {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 25px;
}

.slideout-sidebar ul li {
  cursor: pointer;
  padding: 6px 0;
  border-bottom: 1px solid rgba(244, 244, 244, 0.4);
  color: rgba(244, 244, 244, 0.7);
}

.slideout-sidebar ul li a {}


.slideout-sidebar ul li:last-child {
  border-bottom: 0;
}

.slideout-sidebar ul li:hover {
  color: rgba(244, 244, 244, 1);
}


/*-- The Magic --*/
#menu-toggle {
  visibility: hidden;
}

.menu-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  z-index: 1;
  transition: all 300ms ease-in-out;
}

/*-- The Magic --*/
#menu-toggle:checked~.slideout-sidebar {
  left: 0px;
}

#menu-toggle:checked+.menu-icon {}


/*
 * Just a quick hamburger
 */
#menu-toggle-label {
  z-index: 10;
  position: fixed;
}

#menu-toggle-label span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #000;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0),
    opacity 0.55s ease;
}

#menu-toggle-label span:first-child {
  transform-origin: 0% 0%;
}

#menu-toggle-label span:nth-last-child(2) {
  transform-origin: 0% 100%;
}

/*
  * Transform all the slices of hamburger
  * into a crossmark.
  */
#menu input:checked~#menu-toggle-label>span {
  opacity: 1;
  transform: rotate(-45deg) translate(-2px, -1px);
  background: #fff;
}

/*
  * But let's hide the middle one.
  */
#menu input:checked~#menu-toggle-label>span:nth-last-child(2) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

/*
  * Ohyeah and the last one should go the other direction
  */
#menu input:checked~#menu-toggle-label>span:nth-last-child(3) {
  transform: rotate(45deg) translate(0, -1px);
}

